					let code = loginRes.code;
					//_this.$api.login({jsCode: code})
					_this.$api.getUserInfo('5s5ge52xg1a3g').then(res => {
						console.log(res)
						_this.pers_card_msg = res.data
						if(res.data.bgImg) {
							_this.bgImageURL = res.data.bgImg
						}
						
					})
					console.log(code)
				}
			});
		},
